/* ChatWidget.css */

/* Wrapper stays bottom-right with bubble below */
.chat-widget-wrapper {
  position: fixed;
  bottom: 24px;
  right: 24px;
  z-index: 1000;
  display: flex;
  flex-direction: column; /* popup above, bubble below */
  align-items: flex-end;
  gap: 12px; /* space between popup and bubble */
}

/* Popup container: fixed height, flex layout, no overflow hidden */
.chat-widget-popup {
  width: 380px;
  height: 600px; /* lock in max height */
  background: #ffffff;
  border-radius: 16px;
  box-shadow: 0 12px 32px rgba(0, 0, 0, 0.12);

  display: flex;
  flex-direction: column;
  overflow: unset; /* allow inner scrolling */

  animation: slideUp 0.3s ease-out;
}

@keyframes slideUp {
  from {
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

/* Scrollable message area */
.chat-box {
  flex: 1 1 auto; /* fill available space */
  overflow-y: auto; /* show vertical scrollbar when needed */
  padding: 8px;
}

/* Input form pinned to bottom */
.chat-form {
  flex: 0 0 auto; /* fixed size at bottom */
  padding: 8px;
  border-top: 1px solid #eee;
  background: #fafafa;
}

/* Circular bubble toggle */
.chat-bubble {
  width: 64px;
  height: 64px;
  border: none;
  border-radius: 50%;
  background: linear-gradient(135deg, #2d3b8f, #4e5dbb);
  color: #fff;
  font-size: 1.6rem;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  transition:
    transform 0.2s ease,
    box-shadow 0.2s ease;
}

.chat-bubble:hover {
  transform: scale(1.1);
  box-shadow: 0 10px 32px rgba(0, 0, 0, 0.3);
}
